import React, { useEffect } from "react";
import PropTypes from "prop-types";
import { makeStyles } from "@material-ui/core/styles";
import Tabs from "@material-ui/core/Tabs";
import Tab from "@material-ui/core/Tab";
import Typography from "@material-ui/core/Typography";
import Box from "@material-ui/core/Box";
import About from "../pages/about";
import Experience from "../pages/experience";
import Education from "../pages/education";
import Illustrations from "../pages/illustration";
import DigitalWork from "../pages/digitalWork";

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div role="tabpanel" hidden={value !== index} id={`vertical-tabpanel-${index}`} aria-labelledby={`vertical-tab-${index}`} {...other}>
      {value === index && (
        <Box p={5}>
          <Typography>{children}</Typography>
        </Box>
      )}
    </div>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.any.isRequired,
  value: PropTypes.any.isRequired,
};

function a11yProps(index) {
  return {
    id: `vertical-tab-${index}`,
    "aria-controls": `vertical-tabpanel-${index}`,
  };
}

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    backgroundColor: theme.palette.background.paper,
    display: "flex",
    height: "100vh",
    width: "inherit",
  },
  tabs: {
    background: "#bd5d38",
    width: "250px",
    color: "white",
    borderRight: `1px solid ${theme.palette.divider}`,
  },
}));

export default function VerticalTabs() {
  const classes = useStyles();
  const [value, setValue] = React.useState(0);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  useEffect(() => {
    a11yProps(1);
    setValue(1);
  }, []);

  return (
    <div className={classes.root}>
      <div className="h-100 d-flex justify-content-center">
        <Tabs orientation="vertical" variant="scrollable" value={value} onChange={handleChange} aria-label="Vertical tabs example" className={classes.tabs}>
          <div className="text-center py-5">
            <img src={require("../assets/img/profile.jpg")} alt="mansi-lohar" className="profile_img" />
          </div>
          <Tab label="About" {...a11yProps(1)} />
          <Tab label="Experience" {...a11yProps(2)} />
          <Tab label="Education" {...a11yProps(3)} />
          <Tab label="Illustration" {...a11yProps(4)} />
          <Tab label="Digital Work" {...a11yProps(5)} />
        </Tabs>
      </div>
      <TabPanel value={value} index={1}>
        <About />
      </TabPanel>
      <TabPanel value={value} index={2}>
        <Experience />
      </TabPanel>
      <TabPanel value={value} index={3}>
        <Education />
      </TabPanel>
      <TabPanel value={value} index={4}>
        <Illustrations />
      </TabPanel>
      <TabPanel value={value} index={5}>
        <DigitalWork />
      </TabPanel>
    </div>
  );
}
